import {
	baseUrl
} from "../config/index"

export const request = (options) => {
	return new Promise((resolve, reject) => {
		// uni.showLoading({
		// 	title: ""
		// })"/dev-api"
		uni.request({
			url: baseUrl + options.url,
			method: options.method || 'post',
			header: {},
			data: options.data || {},
			success: (res) => {
				if (!uni.getStorageSync('sessionid') || res.data.status == 1) {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}


				resolve(res)
			},
			fail: (err) => {
				console.log('');
				reject(rej)
			},
			complete: () => {
				// console.log("");
				uni.hideLoading({
					success: (res) => {}
				})
			}

		})
	})
}